#Home {
    display: grid;
    grid-auto-flow: row;
    place-items: center;
    padding-left: 15%;
    padding-right: 15%;
    background-image: url('https://raw.githubusercontent.com/KleemoffDeveloper/fullstack-portfolio-frontend/main/public/Spider-Verse-Graphic.png');
}

@media screen and (max-width: 1000px) {
  #Home {
    padding: 0;
  }
}

#Welcome-Message {
    margin-top: 150px;
    margin-bottom: 150px;
    text-align: center;
}

#Welcome-Message {
    font-size: clamp(5px, 4vw, 35px);
}

#Home-Display {
    display: grid;
    grid-auto-flow: column;
    width: 100%;
    padding: 15px;
}

#Home-Loading {
    display: grid;
    grid-auto-flow: row;
    gap: 10px;
    place-items: center;
}

.rotate {
    animation: rotateAnimation 2s linear infinite;
  }

  @keyframes rotateAnimation {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }